* {
  border: 0;
  margin: 0;
  box-sizing: border-box;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}
.header Button {
  height: 2rem;
}

.btn {
  color: aliceblue;
  background-color: #000;
  border-radius: 5px;
  padding-inline: 1rem;
}

.container {
  margin: 20px;
  border-top: 1px solid #000;
  border-right: 1px solid #000;
  border-bottom: 1px solid rgb(15, 199, 227);
  border-left: 1px solid rgb(15, 199, 227);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 1rem;
  border-radius: 9px;
  max-width: 500px;
}
.container .tete {
  margin-inline: 20px;
  background-color: rgb(210, 210, 210);
  padding: 0.5rem;
  margin-top: 2px;
}
.container .tete h3, .container .tete p {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 1rem;
}
.container .tete:last-child {
  border-radius: 0 0 10px 10px;
}

.greene {
  border-left: 5px solid green;
}

.form-control {
  display: flex;
  flex-direction: column;
  margin: 20px;
}
.form-control label {
  font-size: large;
  margin-bottom: 6px;
}

input {
  border: #000 1px solid;
  height: 2rem;
  width: 100%;
  padding: 4px 7px;
}

.btne {
  display: block;
  width: calc(100% - 40px);
  margin-left: 20px;
  background-color: #000;
  color: aliceblue;
}

.add-form {
  margin-bottom: 30px;
}

.form-controlboxcheck {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.7rem;
}
.form-controlboxcheck label {
  margin: 0;
}
.form-controlboxcheck input {
  width: 1rem;
}/*# sourceMappingURL=index.css.map */